body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

html{
  height:100%;
}
code {
 font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
   monospace;
}

#root {
 height:100%;
}
.App {
  text-align: center;
  height:100%;
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}


.registerMain {
    background:-webkit-linear-gradient(315deg, rgb(82, 221, 226) 0%, rgb(33, 173, 108) 100%);
    background:linear-gradient(135deg, rgb(82, 221, 226) 0%, rgb(33, 173, 108) 100%);
    height: 100vh;
    padding:0;
    margin:0;
    /* background-color: black; */
}

.register {
    position: absolute;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background-color: white;
    box-shadow: 5px 5px 8px #888888;
}

input {
    border: none !important;
    border-bottom: 2px solid teal !important;
    border-radius: none;
}

.inputFile {
    border: none !important;
}

.paddingX {
    padding-left: 100px;
    padding-right: 100px;

}

.imageSignUp {
       background-image: url(/static/media/38d10185a98b.6ac9e781.jpeg);
       background-position:center;
       background-repeat: no-repeat;
       background-size: cover;
       background-attachment: fixed;
     
       
   }

   .lableSex {
       font-weight: 600;  
   }
   .btnRegister {
       background:-webkit-linear-gradient(315deg, rgb(151, 230, 233) 0%, rgb(172, 233, 103) 100%);
       background:linear-gradient(135deg, rgb(151, 230, 233) 0%, rgb(172, 233, 103) 100%);
   }

   .btnRegister:hover {
       background: -webkit-linear-gradient(315deg, rgb(82, 221, 226) 0%, rgb(33, 173, 108) 100%);
       background: linear-gradient(135deg, rgb(82, 221, 226) 0%, rgb(33, 173, 108) 100%);
       color: white;
   }

   @media only screen and (max-width: 374px){
       body{
           font-size: 12px;
        }
        h2 {
            font-size: 25px;
        }
   }
   @media only screen and (max-width: 700px){
    .paddingX {
        padding-left: 50px;
        padding-right: 50px;
    }
    
   }

   @media only screen and (max-width: 575px) {
    .imageSignUp {
        display: none;
    }
   }
